import footerBar from '../components/footerBar'
export default {
  name: 'toast',
  components: { footerBar },
  data () {
    return {

    }
  },
  methods: {
    successTip () {
      this.$toast({
        msg: '成功提示',
        type: 'success'
      })
    },
    failTip () {
      this.$toast({
        msg: '失败提示',
        type: 'fail'
      })
    },
    warningTip () {
      this.$toast({
        msg: '警示提示',
        type: 'warning'
      })
    },
    loadingTip () {
      this.$toast({
        msg: '正在加载',
        type: 'loading'
      })
    },
    oneTip () {
      this.$toast({
        msg: '手机号码不能为空'
      })
    },
    multiTip () {
      this.$toast({
        msg: '字数多怎么办字数多怎么办哈哈哈'
      })
    }
  }
}



